<template>
  <div class="new-article">
    <div class="top">
      <div>
        <h2>dy.tcualhp.cn</h2>
        <h2></h2>
      </div>
      <button
        :disabled="(title && category) ? null : 'disbaled'"
        @click="post"
      ></button>
    </div>
    <div class="tips">
      <div class="editor">
        <div class="category">
          <h3></h3>
          <select v-model="category">
            <option value="prediction"></option>
            <option value="stock"></option>
            <option value="report"></option>
            <option value="investment"></option>
            <option value="research"></option>
          </select>
        </div>
        <div class="title">
          <h3></h3>
          <input
            type="text"
            v-model="title"
          >
        </div>
        <div class="description">
          <h3 class="i"></h3>
          <textarea
            type="textarea"
            v-model="description"
          />
          </div>
    </div>
    <div class="cover">
      <h3></h3>
      <div class="image"><img :src="imgUrl"></div>
      <p></p>
      <!-- <button class="c-btn" @click="crop"></button> -->
      <input
        id="cover"
        type="file"
        accept="image/*"
        @change="changeFile"
      >
    </div>
  </div>
  <div class="content-header">
    <h3></h3>
    <mark-down
      @on-save="saveMd"
      ref="md"
      v-model="convalue"
    ></mark-down>
  </div>
  <div class="tip" v-show="showTip">
    <h3>{{tip}}</h3>
  </div>
  <div class="show-crop" v-show="cropper">
				<h3></h3>
				<div class="cropper">
					<vueCropper
						ref="cropper"
						:img="option.img"
						:outputSize="option.size"
						:outputType="option.outputType"
						:info="true"
						:full="option.full"
						:canMove="option.canMove"
						:canMoveBox="option.canMoveBox"
						:fixedBox="option.fixedBox"
						:original="option.original"
						:autoCrop="option.autoCrop"
						:autoCropWidth="option.autoCropWidth"
						:autoCropHeight="option.autoCropHeight"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
						:centerBox="option.centerBox"
						:high="option.high"
						:infoTrue="option.infoTrue"
            :maxImgSize="option.maxImgSize"
						:enlarge="option.enlarge"
						:mode="option.mode"
            :limitMinSize="option.limitMinSize"
					></vueCropper>
				</div>
        <div class="enssure"><button @click="uploadFile"></button></div>
  </div>
  </div>
</template>

<style lang="sass" scoped>
@import '../assets/style/article.scss';
</style>
<script>
import MarkDown from "vue-meditor";
import axios from "axios";
import Cos from "cos-js-sdk-v5";
import fs from "fs";
import { VueCropper } from "vue-cropper";

export default {
  components: {
    MarkDown,
    VueCropper
  },
  data() {
    return {
      title: "",
      category: "",
      coverUrl: "logo.jpg",
      imgUrl:
        "https://dingyang-admin-1301593316.cos.ap-guangzhou.myqcloud.com/coverImg/logo.jpg",
      description: "",
      content: "",
      html:"",
      convalue: "",
      showTip: false,
      tip: "",
      cropper: false,
      option: {
        //img
        img: "",
        size: 1,
        full: false,
        outputType: "png",
        canMove: true,
        fixedBox: false,
        original: false,
        canMoveBox: true,
        autoCrop: true,
        //  
        autoCropWidth: 337,
        autoCropHeight: 440,
        fixed: true, //
        fixedNumber: [229, 299], //
        centerBox: false,
        high: false,
        cropData: {},
        enlarge: 1,
        mode: "contain",
        maxImgSize: 2000,
        limitMinSize: [100, 120]
      }
    };
  },
  methods: {
    saveMd(content) {
      this.html = content.html;
      this.content = content.value;
    },

    async post() {
      this.$refs.md.handleSave();
      let data = {
        title: this.title,
        description: this.description,
        content: this.content,
        type: this.category,
        coverUrl: this.coverUrl
      };

      this.showTip = true;
      this.tip = "...";
      
      const url = "/api/dingyang/article/add";
      var that = this;
      await axios
        .post(url, JSON.stringify(data), {
          headers: {
            "Content-Type": "application/json; charset=UTF-8"
          }
        })
        .then(function(response) {
          that.tip = "";
          setTimeout(function() {
            that.tipCard = false;
            Object.assign(that.$data, that.$options.data());
            that.convalue = "";
          }, 500);
        });
    },

    async changeFile(e) {
      const files = e.target.files;
      if (!files) {
        return;
      }
      const file = files[0];
      var render = new FileReader();
      render.readAsDataURL(file);
      render.onload = () => {
        this.option.img = render.result;
      };
      this.cropper = true;
    },

    uploadFile() {
      const cos = new Cos({
        SecretId: "AKID2yVkjaEMJ0b25XqZ3HlynLbbOuhEcyrT",
        SecretKey: "1mHVUCLMzNUKl2SbLpBk3wZpojES9Zrj"
      });

      let filename = Date.now() + ".jpg";
      this.coverUrl = filename;

      var that = this;
      this.$refs.cropper.getCropBlob(data => {
        let file = new window.File([data], filename, {
          type: data.type
        });
        cos.putObject(
          {
            Bucket: "dingyang-admin-1301593316" /*  */,
            Region: "ap-guangzhou" /*  */,
            Key: "coverImg/" + filename /*  */,
            StorageClass: "STANDARD",
            Body: file // 
          },
          function(err, data) {
            that.imgUrl = "https://" + data.Location;
            that.cropper = false;
            alert("");
          }
        );
      });
    }
  }
};
</script> 


