<template>
  <div class="new-article">
    <div class="top">
      <div>
        <h2>hy.tcualhp.cn</h2>
        <h2></h2>
      </div>
      <button
        :disabled="(convalue && category) ? null : 'disbaled'"
        @click="post"
      ></button>
    </div>
    <div class="tips">
      <div class="editor">
        <div class="category">
          <h3></h3>
          <select v-model="category">
            <option value="about"></option>
            <option value="team"></option>
            <option value="process"></option>
            <option value="manage"></option>
            <option value="adviser"></option>
            <option value="steward"></option>
            <option value="idea"></option>
          </select>
        </div>
    </div>
  </div>
  <div class="content-header">
    <h3></h3>
    <mark-down
      @on-save="saveMd"
      ref="md"
      v-model="convalue"
    ></mark-down>
  </div>
  <div class="tip" v-show="showTip">
    <h3>{{tip}}</h3>
  </div>
  </div>
</template>

<style lang="sass" scoped>
@import '../assets/style/article.scss';
</style>
<script>
import MarkDown from "vue-meditor";
import axios from "axios";
import fs from "fs";

export default {
  components: {
    MarkDown
  },
  data() {
    return {
      category: "",
      content: "",
      html:"",
      convalue: "",
      showTip: false,
      tip: "",
      list: {
        "about": "",
        "team": "",
        "process": "",
        "manage": "",
        "adviser": "",
        "steward": "",
        "idea": ""
      }
    };
  },
  methods: {
    saveMd(content) {
      this.html = content.html;
      this.content = content.value;
    },

    async post() {
      this.$refs.md.handleSave();
      let data = {
        content: this.html,
        type: this.category,
        typeTitle: this.list[this.category]
      };
      console.log(data)

      this.showTip = true;
      this.tip = "...";
      
      const url = "/api/huayin/other/add";
      var that = this;
      await axios
        .post(url, JSON.stringify(data), {
          headers: {
            "Content-Type": "application/json; charset=UTF-8"
          }
        })
        .then(function(response) {
          that.tip = "";
          setTimeout(function() {
            that.tipCard = false;
            Object.assign(that.$data, that.$options.data());
            that.convalue = "";
          }, 500);
        });
    }
  }
};
</script> 


